.container {
    display: grid;
    width: 100%;
    margin: 0 auto;
    height: 500px;
    background-color: rosybrown;

    grid-template-rows: 1fr;
    grid-template-columns: 3fr 8fr;
}

@media screen and (max-width: 500px) {
    .container {
        display: block;
        background-color: #c42c2c;
    }
}